import { CREATE_TESTIMONIALS_RED, DELETE_TESTIMONIALS_RED, GET_TESTIMONIALS_RED, UPDATE_TESTIMONIALS_RED } from "../Constants";


export default function TestimonialsReducer(state=[],action) {
 
    switch (action.type) {
        case CREATE_TESTIMONIALS_RED:
        let newState=[...state]
            newState.push(action.payload)
            return newState
        case GET_TESTIMONIALS_RED:
           return action.payload 
        case UPDATE_TESTIMONIALS_RED:
            let index= state.findIndex((x)=>x._id===action.payload._id) 
            state[index]=action.payload.name
            state[index]=action.payload.pic
            state[index]=action.payload.active
            return state
        case DELETE_TESTIMONIALS_RED:
            return state.filter((x)=>x._id!==action.payload._id)           
            default:
            return state
    }
}
