

import { put, takeEvery } from "redux-saga/effects"
import { createMultipartRecords, deleteRecords, getRecords, updateMultipartRecords,  } from "./Services"
import { CREATE_BRAND, CREATE_BRAND_RED, DELETE_BRAND, DELETE_BRAND_RED, GET_BRAND, GET_BRAND_RED, UPDATE_BRAND, UPDATE_BRAND_RED } from "../Constants"

function* createSaga(action) {
    let response = yield createMultipartRecords("brand", action.payload)
    yield put({ type: CREATE_BRAND_RED, payload: response.data })
}

function* getSaga() {
    let response = yield getRecords("brand")
    yield put({ type: GET_BRAND_RED, payload: response.data })
}

function* updateSaga(action) {
    let response = yield updateMultipartRecords("brand", action.payload)
    yield put({ type: UPDATE_BRAND_RED, payload:response.data })
}

function* deleteSaga(action) {
    let response = yield deleteRecords("brand", action.payload)
    yield put({ type: DELETE_BRAND_RED, payload: response.data })
}

export default function* BrandSagas() {
  
  yield takeEvery(CREATE_BRAND,createSaga)
  yield takeEvery(GET_BRAND,getSaga)
  yield takeEvery(UPDATE_BRAND,updateSaga)
  yield takeEvery(DELETE_BRAND,deleteSaga)
}
