/* eslint-disable no-unused-vars */
import{React,useState,useEffect}  from 'react'
import HeroSection from '../Components/HeroSection'
import formValidators from '../Components/Validators/formValidators'
import imageValidators from '../Components/Validators/imageValidators'
import { useNavigate } from 'react-router-dom'

export default function UpdateProfile() {
    let [data, setData] = useState({
           name: "",
           phone: "",
           address: "",
           pin: "",
           city: "",
           state: ""
       })
       let [errorMessage, setErrorMessage] = useState({
           name: "",
           phone: "",
           pic: ""
       })
       let [show, setShow] = useState(false)
   
       let navigate = useNavigate()
   
       function getInputData(e) {
           var name = e.target.name
          let value = e.target.files ? e.target.files[0] : e.target.value  //in case of real backend //for pic i will decleare new variable formdata
           setErrorMessage((old) => {
               return {
                   ...old,
                   [name]: e.target.files ? imageValidators(e) : formValidators(e)
               }
           })
           setData((old) => {
               return {
                   ...old,
                   [name]: value
               }
           })
       }
   
       async function postData(e) {
           e.preventDefault()
               let error = Object.values(errorMessage).find((x) => x !== "")
           if (error){
               setShow(true)
           }
        else {
            try {
                let formData = new FormData()
                formData.append("_id", data._id)
                formData.append("name", data.name)
                formData.append("phone", data.phone)
                formData.append("address", data.address)
                formData.append("pin", data.pin)
                formData.append("city", data.city)
                formData.append("state", data.state)
                formData.append("pic", data.pic)
                let response = await fetch(`${process.env.REACT_APP_SERVER}/api/user/${localStorage.getItem("userid")}`, {
                    method: "PUT",
                    headers: {
                        // "content-type": "application/json",
                        "authorization": localStorage.getItem("token")
                    },
                    // body: JSON.stringify({ ...data })
                    body:formData
                })
                response = await response.json()
                if (response.result === "Done") {
                    if (data.role === "Buyer")
                        navigate("/profile")
                    else
                        navigate("/admin")
                }
                else
                    alert("Something Went Wrong")
            }
            catch (error) {
                alert("Internal Server Error")
            }
        }
    }
       
    //we have to fetch get data from api
       useEffect(() => {
             (async () => {
            try {
                let response = await fetch(`${process.env.REACT_APP_SERVER}/api/user/${localStorage.getItem("userid")}`, {
                    method: "GET",
                    headers: {
                        "content-type": "application/json",
                        "authorization": localStorage.getItem("token")
                    }
                })
                response = await response.json()
                if (response.result === "Done")
                    setData(response.data)
            } catch (error) {
                alert("Internal Server Error")
            }
        })()
       }, [])
   

 
  return (
    < >
        <HeroSection title="update your profile"/>
        <div className="container">
        <div className="row">
            <div className="col-md-8 col-sm-10 m-auto">{/* smallscreen :-sc md:- mediem screen*/}
              <h5 className='bg-primary text-light text-center p-2'> Update Your Profile</h5>
              <form onSubmit={postData}>

                <div className="row">
                  <div className="col-md-6 mb-3">
                    <lable>Name*</lable>
                    <input type="text" name="name" onChange={getInputData} className={`form-control border-3 ${show && errorMessage.name? 'border-danger' : 'border-primary' }`} value={data.name} placeholder="Enter your full name" />
                    {show && errorMessage.name? <p className='text-danger'>{errorMessage.name}</p> :null}
                  </div>
                  
                  <div className="col-md-6 mb-3">
                     <label>Phone Number*</label>
                     <input type="text" name="phone" onChange={getInputData}  className={`form-control border-3 ${show && errorMessage.phone ? 'border-danger' : 'border-primary'}`} value={data.phonenumber} placeholder='Phone Number' />
                      {show && errorMessage.phone ? <p className='text-danger'>{errorMessage.phone}</p> : null}
                       </div>
                  </div>
                  <div className="mb-3">
                                <label>Address</label>
                                <textarea name="address" onChange={getInputData} className='form-control border-3 border-primary' rows={3} placeholder='Address...' value={data.address}></textarea>
                            </div>

                            <div className="row">
                                <div className="col-md-6 mb-3">
                                    <label>City</label>
                                    <input type="text" name="city" onChange={getInputData} className='form-control border-3 border-primary' value={data.city} placeholder='City Name' />
                                </div>

                                <div className="col-md-6 mb-3">
                                    <label>State</label>
                                    <input type="text" name="state" onChange={getInputData} className='form-control border-3 border-primary' value={data.state} placeholder='State Name' />
                                </div>
                            </div>

                            <div className="row">
                                <div className="col-md-6 mb-3">
                                    <label>Pin</label>
                                    <input type="text" name="pin" onChange={getInputData} className='form-control border-3 border-primary' value={data.pin} placeholder='Pin Code' />
                                </div>

                                <div className="col-md-6 mb-3">
                                    <label>Pic</label>
                                    <input type="file" name="pic" onChange={getInputData} className='form-control border-3 border-primary' />
                                </div>
                            </div>
                 
                         


                  <div className="mb-3">
                  <button type="submit"  className='btn btn-primary w-100'>Update</button>
                  </div>   

                   
                
                </form>
                            
            </div>
    
        </div>
      </div>
    </ >
  )
}
