import { put, takeEvery } from "redux-saga/effects";
import { CREATE_TESTIMONIALS, CREATE_TESTIMONIALS_RED, DELETE_TESTIMONIALS, DELETE_TESTIMONIALS_RED, GET_TESTIMONIALS, GET_TESTIMONIALS_RED, UPDATE_TESTIMONIALS, UPDATE_TESTIMONIALS_RED } from "../Constants"
// import { createRecords, deleteRecords, getRecords, updateRecords } from "./Service/ApiCallingService"
import { createMultipartRecords, deleteRecords, getRecords, updateMultipartRecords } from "./Services/index"


function* createSaga(action) {                          //worker saga or executer saga
    // let response = yield createRecords("testimonialS", action.payload)
    let response = yield createMultipartRecords("testimonials", action.payload)
    yield put({ type: CREATE_TESTIMONIALS_RED, payload: response.data })
}

function* getSaga(action) {                             //worker saga or executer saga
    let response = yield getRecords("testimonials")
    yield put({ type: GET_TESTIMONIALS_RED, payload: response.data })
}

function* updateSaga(action) {                          //worker saga or executer saga
    // yield updateRecords("testimonialS", action.payload)
    // yield put({ type: UPDATE_TESTIMONIALS_RED, payload: action.payload })
    let response = yield updateMultipartRecords("testimonials", action.payload)
    yield put({ type: UPDATE_TESTIMONIALS_RED, payload: response.data })
}

function* deleteSaga(action) {                          //worker saga or executer saga
    yield deleteRecords("testimonials", action.payload)
    yield put({ type: DELETE_TESTIMONIALS_RED, payload: action.payload })
}


export default function* TestimonialSagas() {
    yield takeEvery(CREATE_TESTIMONIALS, createSaga)    //watcher saga
    yield takeEvery(GET_TESTIMONIALS, getSaga)          //watcher saga
    yield takeEvery(UPDATE_TESTIMONIALS, updateSaga)    //watcher saga
    yield takeEvery(DELETE_TESTIMONIALS, deleteSaga)    //watcher saga
}