import React, { useEffect } from 'react';


import Testimonials from '../Components/Testimonials'
import { Link } from 'react-router-dom'
import Product from '../Components/Product'
import Facts from '../Components/Facts'
import CategorySlider from "../Components/CategorySlider"

import { useDispatch , useSelector } from 'react-redux'

import {getMaincategory}from '../Redux/Actionscreators/MaincategoryActionCreators'
import {getSubcategory}from '../Redux/Actionscreators/SubcategoryActionCreators'
import {getBrand}from '../Redux/Actionscreators/BrandActionCreators'
import {getProduct}from '../Redux/Actionscreators/ProductActionCreators'

export default function Home() {


   let dispatch = useDispatch()
   let MaincategoryStateData= useSelector(state =>state.MaincategoryStateData)
   let SubcategoryStateData= useSelector(state =>state.SubcategoryStateData)
   let BrandStateData= useSelector(state =>state.BrandStateData)
   let ProductStateData= useSelector(state =>state.ProductStateData)

 useEffect(() => {
        (() => {
            dispatch(getMaincategory())
        })()
    }, [MaincategoryStateData.length])

    useEffect(() => {
        (() => {
            dispatch(getSubcategory())
        })()
    }, [SubcategoryStateData.length])

    useEffect(() => {
        (() => {
            dispatch(getBrand())
        })()
    }, [BrandStateData.length])

    useEffect(() => {
        (() => {
            dispatch(getProduct())
        })()
    }, [ProductStateData.length])
  return (
    <>

   
  { /*<!-- Carousel Start -->*/}
    <div className="container-fluid p-0 wow fadeIn" data-wow-delay="0.1s">
        <div id="header-carousel" className="carousel slide" data-bs-ride="carousel">
            <div className="carousel-inner">
                <div className="carousel-item active">
                    <img className="w-100" src="img/img4.jpg" alt=""/>
                    <div className="carousel-caption">
                        <div className="container">
                            <div className="row justify-content-center">
                                <div className="col-lg-7">
                                    <h1 className="display-2 text-light mb-5 animated slideInDown">🛒 "Best Deals On Top Brands – Shop Now!</h1>
                                    <Link to="/shop" className="btn btn-primary py-sm-3 px-sm-5">SHOP</Link>
                                    <Link to="#" className="btn btn-light py-sm-3 px-sm-5 ms-3">View Deals</Link>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="carousel-item">
                    <img className="w-100" src="img/img11.jpg" alt=""/>
                    <div className="carousel-caption">
                        <div className="container">
                            <div className="row justify-content-center">
                                <div className="col-lg-7">
                                    <h1 className="display-2 text-light mb-5 animated slideInDown">🌟 New Arrivals! Latest Fashion Trends</h1>
                                    <Link href="#" className="btn btn-primary py-sm-3 px-sm-5">Explore Categories</Link>
                                    <Link to="/shop" className="btn btn-light py-sm-3 px-sm-5 ms-3">Shop Now</Link>
                                   
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                
            </div>
            <button className="carousel-control-prev" type="button" data-bs-target="#header-carousel"
                data-bs-slide="prev">
                <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                <span className="visually-hidden" >Previous</span>
            </button>
            <button className="carousel-control-next" type="button" data-bs-target="#header-carousel"
                data-bs-slide="next">
                <span className="carousel-control-next-icon" aria-hidden="true"></span>
                <span className="visually-hidden">Next</span>
            </button>
        
        </div>
    </div>
    <Facts/>
    {/*<!-- Carousel End -->*/}
     {/* second step :-we will put product in  maincategory subcategory and in brand and these lines or for display things*/}
      {/* third step:-we will  create a categorySlider for categories to make it slide*/}
        
         <CategorySlider title="Maincategory" data={MaincategoryStateData.filter(x => x.active)} />
           
            <CategorySlider title="Subcategory" data={SubcategoryStateData.filter(x => x.active)} />
            
            {MaincategoryStateData.filter(x => x.active).map((item) => {
                return <Product title={item.name} data={ProductStateData.filter(x => x.active && x.maincategory?.name === item.name).slice(0, 12)} />
            })}
           
            <CategorySlider title="Brand" data={BrandStateData.filter(x => x.active)} />
            <Testimonials />
    </>
  )
}
