import React, { useState } from 'react'

import HeroSection from "../Components/HeroSection"

import {useNavigate } from 'react-router-dom'
export default function ForgetPasswordPage1() {
    let [username, setUsername] = useState("")
    let [errorMessage, setErrorMessage] = useState(false)

    let navigate = useNavigate()

    function getInputUsername(e) {
        setUsername(e.target.value)
    }

    async function postUsername(e) {
        e.preventDefault()
    try {
            let response = await fetch(`${process.env.REACT_APP_SERVER}/api/user/forget-password-1`, {
            method: "POST",
            headers: {
                "content-type": "application/json"
            },
             body: JSON.stringify({  username:username })
        })
        response = await response.json()
            if (response.result === "Done") {
                localStorage.setItem("reset-password-username", username)
                navigate("/forget-password-2")
              }      
           
        
        else {
                setErrorMessage("User Not Found")
            }
       
    } catch (error) {
        alert("Internal Server Error")
    }
    }

    return (
        <>
            <HeroSection title="Reset Password" />

            <div className="container my-3">
                <div className="row">
                    <div className="col-md-6 col-sm-8 m-auto">
                        <h5 className='bg-primary text-light text-center p-2'>Reset Password</h5>
                        <form onSubmit={postUsername}>
                            <div className="mb-3">
                                <label>User Name*</label>
                                <input type="text" name="username" onChange={getInputUsername} className={`form-control border-3 ${errorMessage ? 'border-danger' : 'border-primary'}`} placeholder='User Name or Registered Email Address' />
                                {errorMessage ? <p className='text-danger'>Invalid Username or Password</p> : null}
                            </div>
                              <div className="mb-3">
                                <button type="submit" className='btn btn-primary w-100'>Send OTP</button>
                            </div>
                           </form>
                         </div>
                </div >
            </div >
        </>
    )
}
