import React, { useState } from 'react'

import HeroSection from "../Components/HeroSection"

import { useNavigate } from 'react-router-dom'
export default function ForgetPasswordPage3() {
    let [data, setData] = useState({
        password: "",
        cpassword: ""
    })
    let [errorMessage, setErrorMessage] = useState(false)

    let navigate = useNavigate()

    function getInputData(e) {
        var { name, value } = e.target
        setData((old) => {
            return {
                ...old,
                [name]: value
            }
        })
    }
async function postData(e) {
        e.preventDefault()
        if (data.password === data.cpassword) {
            try {
                let response = await fetch(`${process.env.REACT_APP_BACKEND_SERVER}/api/user/forget-password-3`, {
                    method: "POST",
                    headers: {
                        "content-type": "application/json"
                    },
                    body: JSON.stringify({ username: localStorage.getItem("reset-password-username"), password: data.password })
                })
                response = await response.json()
                if (response.result === "Done") {
                    localStorage.removeItem("reset-password-username")
                    navigate("/login")
                }
                else {
                    setErrorMessage(response.reason)
                }
            } catch (error) {
                alert("Internal Server Error")
            }
        }
        else
            setErrorMessage("Password and Confirm Password Doesn't Matched")
    }

    function change(){
        var input = document.getElementById("password")    // THIS  LINES ARE FOR THE EYE OPTION ON WRITE SIDE OF PASSWORD
        var eye = document.getElementById("eye")
        if(input.type==="password"){
            input.type="text"
            eye.classList.remove("fa-eye")
            eye.classList.add("fa-eye-slash")
        }
        else{
            input.type="password"
            eye.classList.remove("fa-eye-slash")
            eye.classList.add("fa-eye")
        }
    }

    return (
        <>
            <HeroSection title="RESET-PASSWORD" />

            <div className="container my-3">
                <div className="row">
                    <div className="col-md-6 col-sm-8 m-auto">
                        <h5 className='bg-primary text-light text-center p-2'>Login to Your Account</h5>
                        <form onSubmit={postData}>
                        

                            <div className="mb-3">
                                <label>Password*</label>
                                <div className="bth-group d-flex">
                                   
                                    <input type="password" name="password" id='password' onChange={getInputData} className="form-control border-3 border-primary" placeholder='Password' />
                                    {errorMessage ? <p className='text-danger'>{errorMessage}</p> : null}
                                    <button type='button' className='btn btn-primary' onClick={change}><i className='fa fa-eye' id='eye'></i></button>
                                </div>
                            </div>
                            
                            <div className="mb-3">
                                <label> Confirm Password*</label>
                                <div className="bth-group d-flex">
                                    
                                    <input type="password" name="cpassword" id='password' onChange={getInputData} className="form-control border-3 border-primary" placeholder='Confirm Password' />
                                     {errorMessage ? <p className='text-danger'>{errorMessage}</p> : null}
                                    <button type='button' className='btn btn-primary' onClick={change}><i className='fa fa-eye' id='eye'></i></button>
                                </div>
                            </div>

                            <div className="mb-3">
                                <button type="submit" className='btn btn-primary w-100'>Reset Password</button>
                            </div>
                        </form>
                     
                    </div>
                </div >
            </div >
        </>
    )
}
