
import React, { useEffect} from 'react'
import { useDispatch, useSelector } from 'react-redux';

import $ from 'jquery';  // Import jQuery
import 'datatables.net-dt/css/dataTables.dataTables.min.css'; // Import DataTables styles
import 'datatables.net';


import Sidebar from '../../Components/Sidebar'
import HeroSection from '../../Components/HeroSection'
import { Link } from 'react-router-dom'

import { deleteBrand, getBrand } from "../../Redux/Actionscreators/BrandActionCreators"
export default function AdminBrand() {
  

  let dispatch = useDispatch()
  let BrandStateData = useSelector((state) => state.BrandStateData)

  function deleteRecord(_id) {
    if (window.confirm("Are You Sure to Delete that Item : ")) {
      dispatch(deleteBrand({ _id: _id }))
      getAPIData()
    }
  }


 function getAPIData() {
        dispatch(getBrand())
        let time = setTimeout(() => {
            $('#DataTable').DataTable()
        }, 500)
        return time
    }
    useEffect(() => {
        let time = getAPIData()
        return () => clearTimeout(time)
    }, [BrandStateData.length])
  return (
    <>
      <HeroSection title="Admin" />
      <div className="container-fluid">
        <div className='row'>
          <div className="col-md-3">
            <Sidebar />
          </div>
          <div className="col-md-9">
            <h5 className='bg-primary text-center text-light p-2'>Brand <Link to="/admin/brand/create"> <i className='fa fa-plus text-light float-end'></i></Link></h5>
            <table className='table table-bordered table-hover table-striped' id='DataTable'>
              <thead>
                <tr>
                  <th>ID</th>
                  <th>Name</th>
                  <th>Pic</th>
                  <th>Active</th>
                  <th>Create</th>
                  <th>Delete</th>
                </tr>
              </thead>
              <tbody>
                {
                  BrandStateData.map((item) => {
                    return <tr key={item._id}>
                      <td>{item._id}</td>
                      <td>{item.name}</td>
                      <td>
                        <Link to={`${process.env.REACT_APP_SERVER}/${item.pic}`} target='_blank' rel='noreferrer'>
                          <img src={`${process.env.REACT_APP_SERVER}/${item.pic}`} height={50} width={80} className='rounded' alt="" />
                        </Link>
                      </td>
                      <td>{item.active ? "Yes" : "No"}</td>
                      <td><Link to={`/admin/brand/update/${item._id}`} className='btn btn-primary'><i className='fa fa-edit'></i></Link></td>
                      <td><button className='btn btn-danger' onClick={() => deleteRecord(item._id)}><i className='fa fa-trash'></i></button></td>
                    </tr>
                  })
                }
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </>
  )
}

