import React, { useEffect, useState } from 'react'
import { Link, useNavigate, useParams } from 'react-router-dom'

import Sidebar from '../../Components/Sidebar'
import HeroSection from '../../Components/HeroSection'

import formValidators from '../../Components/Validators/formValidators'
import imageValidators from '../../Components/Validators/imageValidators'
import { useDispatch,useSelector  } from 'react-redux';
import {getSubcategory, updateSubcategory} from "../../Redux/Actionscreators/SubcategoryActionCreators"

export default function AdminUpdatesubcategory() {
  let { _id } = useParams()
 
  let [data, setData] = useState({
    name: "",
    pic: "",
    active: true
  })
  let [errorMessage, setErrorMessage] = useState({
    name: "",
    pic: ""
  })
  let [show, setShow] = useState(false)
  let navigate = useNavigate()
  
   let dispatch = useDispatch()
    let SubcategoryStateData = useSelector(state => state.SubcategoryStateData) //use selector is used to fetch data from states

  function getInputData(e) {
    let name = e.target.name
    //let value = e.target.files ? "/subcategory/" + e.target.files[0].name : e.target.value
     let value = e.target.files ? e.target.files[0] : e.target.value
    if (name !== "active") {
      setErrorMessage((old) => {
        return {
          ...old,
          [name]: e.target.files ? imageValidators(e) : formValidators(e)
        }
      })
    }
    setData((old) => {
      return {
        ...old,
        [name]: name === "active" ? (value === "1" ? true : false) : value
      }
    })
  }
   function postData(e) {
    e.preventDefault()
    let error = Object.values(errorMessage).find((x) => x !== "")
    if (error)
      setShow(true)
    else {
       let item = SubcategoryStateData.find(x => x._id !== _id && x.name.toLocaleLowerCase() === data.name.toLocaleLowerCase())  // in case of real backend
      if (item) {
        setShow(true)
        setErrorMessage((old) => {
          return {
            ...old,
            'name': "subcategory Name is Already Exist"
          }
        })
      }
      else {
           //dispatch(updateSubcategory({...data}))
             let formData = new FormData()
                formData.append("_id",data._id)  //use id in case of RDBMS and use _id in case of mongodb
                formData.append("name",data.name)
                formData.append("pic",data.pic)
                formData.append("active",data.active)
                dispatch(updateSubcategory(formData))
           navigate("/admin/subcategory")
      }
    }
  }

  useEffect(() => {
    ( () => {
       dispatch(getSubcategory())
      if (SubcategoryStateData.length) {
                // let item = SubcategoryStateData.find(x => x.id === id)
                let item = SubcategoryStateData.find(x => x._id === _id) //in case of real backend
                if (item)
                    setData({ ...item })
            }
        })()
  }, [SubcategoryStateData.length])
  return (
    <>
      <HeroSection title="Admin" />
      <div className="container-fluid">
        <div className='row'>
          <div className="col-md-3">
            <Sidebar />
          </div>
          <div className="col-md-9">
            <h5 className='bg-primary text-center text-light p-2'>Update subcategory <Link to="/admin/subcategory"> <i className='fa fa-backward text-light float-end'></i></Link></h5>
            <form onSubmit={postData}>

              <div className="mb-3">
                <label>Name*</label>
                <input type="text" name="name" value={data.name} onChange={getInputData} className={`form-control border-3 ${show && errorMessage.name ? 'border-danger' : 'border-primary'}`} placeholder='subcategory Name' />
                {show && errorMessage.name ? <p className='text-danger text-capitalize'>{errorMessage.name}</p> : null}
              </div>

              <div className="row">
                <div className="col-md-6 mb-3">
                  <label>Name*</label>
                  <input type="file" name="pic" onChange={getInputData} className={`form-control border-3 ${show && errorMessage.pic ? 'border-danger' : 'border-primary'}`} placeholder='subcategory Name' />
                  {show && errorMessage.pic ? <p className='text-danger text-capitalize'>{errorMessage.pic}</p> : null}
                </div>

                <div className="col-md-6 mb-3">
                  <label>Active*</label>
                  <select name="active" value={data.active ? "1" : "0"} onChange={getInputData} className='form-select border-3 border-primary'>
                    <option value="1">Yes</option>
                    <option value="0">No</option>
                  </select>
                </div>
              </div>

              <div className="mb-3">
                <button type="submit" className='btn btn-primary w-100'>Update</button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </>
  )
}
