import { all } from "redux-saga/effects";

import maincategorySaga from "./MaincategorySagas";
import subcategorySagas  from "./SubcategorySagas"
import brandSagas from "./BrandSagas";
import ProductSaga from "./ProductSaga";
import TestimonialsSaga from"./TestimonialsSaga"
import CartSaga from "./CartSaga";
import WishlistSaga from './WishlistSaga'
import CheckoutSaga from './CheckoutSaga'
import ContactUsSaga from './ContactUsSaga'
import NewsletterSaga from './NewsletterSaga'

export default function* RootSaga() {
    yield all([
       maincategorySaga(),
       subcategorySagas(),
       brandSagas(),
       ProductSaga(),
       TestimonialsSaga(),
       CartSaga(),
       WishlistSaga(),
       CheckoutSaga(),
       ContactUsSaga(),
       NewsletterSaga(),

        
    ])
}