import React from 'react'
import { Link } from 'react-router-dom'

export default function OrderProducts(props) {
  return (
    <>
      <div className="table-responsive">
                <table className='table table-bordered table-striped table-hover'>
                    <thead>
                        <tr>
                            <th></th>
                            <th>Name</th>
                            <th>Brand</th>
                            <th>Color</th>
                            <th>Size</th>
                            <th>Price</th>
                            <th>Quantity</th>
                            <th>Total</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            props.data?.map((item) => {
                                return <tr key={item._id}>
                                    <td>
                                        <Link to={`${process.env.REACT_APP_SERVER}/${item.product?.pic[0]}`} target='_blank' rel='noreferrer'>
                                            <img src={`${process.env.REACT_APP_SERVER}/${item.product?.pic[0]}`} height={50} width={50} className='rounded' alt="Product Image" />
                                        </Link>
                                    </td>
                                    <td>{item.product?.name}</td>
                                    <td>{item.product?.brand.name}</td>
                                    <td>{item.product?.color}</td>
                                    <td>{item.product?.size}</td>
                                    <td>&#8377;{item.product?.finalPrice}</td>
                                    <td>{item.qty}</td>
                                    <td>&#8377;{item.total}</td>
                                </tr>
                            })
                        }
                    </tbody>
                </table>
            </div></>
  )
}
