import React, { useState } from 'react'

import HeroSection from "../Components/HeroSection"

import { Link, useNavigate } from 'react-router-dom'
export default function Login() {
    let [data, setData] = useState({
        username: "",
        password: ""
    })
    let [errorMessage, setErrorMessage] = useState(false)

    let navigate = useNavigate()

    function getInputData(e) {
        var { name, value } = e.target
        setData((old) => {
            return {
                ...old,
                [name]: value
            }
        })
    }

    async function postData(e) {
        e.preventDefault()
    try {
            let response = await fetch(`${process.env.REACT_APP_SERVER}/api/user/login`, {
            method: "POST",
            headers: {
                "content-type": "application/json"
            },
             body: JSON.stringify({ username: data.username, password: data.password })
        })
        response = await response.json()
        if (response.result==="Done" && response.data.active === false) {
                setErrorMessage("Your Account is not Active. Please Contact Us for More Details and To Activate Your Account Please Provide your Username or Registered Email Address in Contact Us Query")
            }
       else if (response.result==="Done") {
             localStorage.setItem("login", true)
                localStorage.setItem("name", response.data.name)
                localStorage.setItem("userid", response.data._id)
                localStorage.setItem("role", response.data.role)
                localStorage.setItem("token", response.token) //we need token
            if (response.data.role === "Buyer")
                navigate("/profile")
            else
                navigate("/admin")
        }
        else {
                setErrorMessage("Invalid Username or Password")
            }
       
    } catch (error) {
        alert("Internal Server Error")
    }
    }

    function change(){
        var input = document.getElementById("password")    // THIS  LINES ARE FOR THE EYE OPTION ON WRITE SIDE OF PASSWORD
        var eye = document.getElementById("eye")
        if(input.type==="password"){
            input.type="text"
            eye.classList.remove("fa-eye")
            eye.classList.add("fa-eye-slash")
        }
        else{
            input.type="password"
            eye.classList.remove("fa-eye-slash")
            eye.classList.add("fa-eye")
        }
    }
    return (
        <>
            <HeroSection title="Login - Access Your Account" />

            <div className="container my-3">
                <div className="row">
                    <div className="col-md-6 col-sm-8 m-auto">
                        <h5 className='bg-primary text-light text-center p-2'>Login to Your Account</h5>
                        <form onSubmit={postData}>
                            <div className="mb-3">
                                <label>User Name*</label>
                                <input type="text" name="username" onChange={getInputData} className={`form-control border-3 ${errorMessage ? 'border-danger' : 'border-primary'}`} placeholder='User Name' />
                                {errorMessage ? <p className='text-danger'>Invalid Username or Password</p> : null}
                            </div>

                            <div className="mb-3">
                                <label>Password*</label>
                                <div className="bth-group d-flex">
                                    <input type="password" name="password" id='password' onChange={getInputData} className="form-control border-3 border-primary" placeholder='Password' />
                                    <button type='button' className='btn btn-primary' onClick={change}><i className='fa fa-eye' id='eye'></i></button>
                                </div>
                            </div>

                            <div className="mb-3">
                                <button type="submit" className='btn btn-primary w-100'>Login</button>
                            </div>
                        </form>
                        <div className='d-flex justify-content-between'>
                            <Link to="/forget-password-1">Forget Password</Link>
                            <Link to="/signup">Don't Heave an Account?Signup</Link>
                        </div>
                    </div>
                </div >
            </div >
        </>
    )
}
