// sideBar or Slider
import React from 'react'
import { Link } from 'react-router-dom'

export default function Sidebar() {
  return (
    <>
     <div className="list-group">
    <Link to="/admin" className="list-group-item list-group-item-action active" aria-current="true"><i className='fa fa-home fs-3'></i> <span className='float-end'>Home</span></Link>
    <Link to="/admin/maincategory" className="list-group-item list-group-item-action action" aria-current="true"><i className='fa fa-list fs-3'></i> <span className='float-end'>MainCategory</span></Link>
    <Link to="/admin/subcategory" className="list-group-item list-group-item-action action" aria-current="true"><i className='fa fa-list fs-3'></i> <span className='float-end'>SubCategory</span></Link>
    <Link to="/admin/brand" className="list-group-item list-group-item-action action" aria-current="true"><i className='fa fa-list fs-3'></i> <span className='float-end'>Brand</span></Link>
    <Link to="/admin/product" className="list-group-item list-group-item-action action" aria-current="true"><i className='fa fa-list fs-3'></i> <span className='float-end'>Product</span></Link>
    <Link to="/admin/testimonials" className="list-group-item list-group-item-action action" aria-current="true"><i className='fa fa-star fs-3'></i> <span className='float-end'>Testimonials</span></Link>
    <Link to="/admin/newsletter" className="list-group-item list-group-item-action action" aria-current="true"><i className='fa fa-envelope fs-3'></i> <span className='float-end'>NewsLetters</span></Link>
    <Link to="/admin/user" className="list-group-item list-group-item-action action" aria-current="true"><i className='fa fa-user fs-3'></i> <span className='float-end'>User</span></Link>
    <Link to="/admin/checkout" className="list-group-item list-group-item-action action" aria-current="true"><i className='fa fa-shopping-bag fs-3'></i> <span className='float-end'>Checkout</span></Link>
    <Link to="/admin/contactus" className="list-group-item list-group-item-action action" aria-current="true"><i className='fa fa-phone fs-3'></i> <span className='float-end'>contactUs</span></Link>




  </div>
    </>
  )
}
