import { put, takeEvery } from "redux-saga/effects";
import { createMultipartRecords, deleteRecords, getRecords, updateMultipartRecords } from "./Services";
import { CREATE_PRODUCT, CREATE_PRODUCT_RED, DELETE_PRODUCT,DELETE_PRODUCT_RED, GET_PRODUCT, GET_PRODUCT_RED,UPDATE_PRODUCT, UPDATE_PRODUCT_RED } from "../Constants";

function* createSaga(action){
 let response = yield createMultipartRecords("product", action.payload)
     yield put({ type: CREATE_PRODUCT_RED, payload: response.data })
}
function* getSaga(){
    let response= yield getRecords("product")
    yield put({type:GET_PRODUCT_RED,payload: response.data })
}
function* updateSaga(action){
    let response = yield updateMultipartRecords("product",action.payload)
    yield put({type:UPDATE_PRODUCT_RED,payload:response.data})
}
function* deleteSaga(action){
    yield deleteRecords("product",action.payload)
    yield put({type:DELETE_PRODUCT_RED,payload:action.payload })
}

export default function* ProductSaga() {
  
  yield takeEvery(CREATE_PRODUCT,createSaga)  
  yield takeEvery(GET_PRODUCT,getSaga)
  yield takeEvery(UPDATE_PRODUCT,updateSaga)
  yield takeEvery(DELETE_PRODUCT,deleteSaga)
}
