import { CREATE_TESTIMONIALS, DELETE_TESTIMONIALS, GET_TESTIMONIALS, UPDATE_TESTIMONIALS } from "../Constants";

export function createTestimonials(data){
  return{
    type:CREATE_TESTIMONIALS,
    payload:data
  }
}
export function getTestimonials() {
    return{
        type : GET_TESTIMONIALS   
        
    }
}
export function updateTestimonials(data) {
    return{
        type :UPDATE_TESTIMONIALS,
        payload:data   
        
    }
}
export function deleteTestimonials(data) {
    return{
        type :DELETE_TESTIMONIALS, 
        payload:data   
        
    }
}
