/* eslint-disable jsx-a11y/img-redundant-alt */
/* eslint-disable react-hooks/exhaustive-deps */
import  React,{useState,useEffect   } from 'react'
import HeroSection from '../Components/HeroSection'
import { useSelector,useDispatch } from 'react-redux'
import { Link } from 'react-router-dom'
import Profile from '../Components/Profile'

import { getWishlist, deleteWishlist } from '../Redux/Actionscreators/WishlistActionCreators'
import { getCheckout } from '../Redux/Actionscreators/CheckoutActioncreators'
import OrderProducts from '../Components/OrderProducts'

export default function ProfilePage() {
  let [wishlist, setWishlist] = useState([])
  let [orders, setOrders]=useState([])
  let dispatch = useDispatch()
  let WishlistStateData = useSelector(state => state.WishlistStateData)
  let CheckoutStateData = useSelector(state => state.CheckoutStateData)
  
  
      function deleteRecord(_id) {
          if (window.confirm("Are You Sure to Delete that Item : ")) {
              dispatch(deleteWishlist({ _id: _id }))
              getAPIData()
          }
      }
  
      function getAPIData() {
          dispatch(getWishlist())
          if (WishlistStateData.length)
              setWishlist(WishlistStateData)
          else
              setWishlist([])
      }
      useEffect(() => {
          getAPIData()
      }, [WishlistStateData.length])
      useEffect(() => {
     (()=>{
            dispatch(getCheckout())
            if (CheckoutStateData.length) {
                setOrders(CheckoutStateData)
            }
     })()
    }, [CheckoutStateData.length])
  
  
     


  return (
    <>
         <HeroSection title="Buyer Profile Section"/>

         <div className="container-fulid my-3">
          <Profile title="Buyer Profile"/>
          <h5 className='bg-primary p-2 text-center text-light'>Wishlist Section</h5>    {/* so we will write condition here agar wishlistlength kuch na kuch haaclass table table-bordered table-striped table-hover*/}      
          {
           wishlist.length?      
            <div className="table-responsive">
                            <table className='table table-bordered table-striped table-hover'>
                                <thead>
                                    <tr>
                                        <th></th>
                                        <th>Name</th>
                                        <th>Brand</th>
                                        <th>Color</th>
                                        <th>Size</th>
                                        <th>Price</th>
                                        <th>Stock Quantity</th>
                                        <th>Cart</th>
                                        <th>Delete</th>
                                    </tr>
                                </thead>
                                 <tbody>
                                  {
                                    wishlist?.map((item) => {
                                    return <tr key={item._id}>
                                    <td>
                                    <Link to={`${process.env.REACT_APP_SERVER}/${item.product?.pic[0]}`} target='_blank' rel='noreferrer'>
                                    <img src={`${process.env.REACT_APP_SERVER}/${item.product?.pic[0]}`} height={50} width={50} className='rounded' alt="Product Image" />
                                    </Link>
                                    </td>
                                    <td>{item.product?.name}</td>
                                    <td>{item.product?.brand.name}</td>
                                    <td>{item.product?.color}</td>
                                    <td>{item.product?.size}</td>
                                    <td>&#8377;{item.product?.finalPrice}</td>
                                    <td>{item.product?.stockQuantity}</td>
                                    <td><Link to={`/product/${item.product._id}`} className='btn btn-primary'><i className='fa fa-shopping-cart'></i></Link></td>
                                    <td><button className='btn btn-danger'onClick={() => deleteRecord(item._id)} ><i className='fa fa-trash'></i></button></td>
                                    </tr>
                                     })
                                  }
                                </tbody>
                                </table>
                                
                                </div>:
                          <div className="text-center card p-5">
                          <h3>No Items in Wishlist</h3>
                         <Link to="/shop" className='btn btn-primary w-25 m-auto'>Shop Now</Link>
                         </div>
                           } 
                                             
         </div>


         <h5 className='bg-primary p-2 text-center text-light'> Orders History Section</h5>         
          {
           orders.length?
           orders.map((item)=>{
            return  <div className="row border-bottom border-5 border-primary" key={item._id}>
                      <div className="col-md-4">
                                    <div className="table-responsive">
                                        <table className='table table-bordered table-striped table-hover'>
                                            <tbody>
                                                <tr>
                                                    <th>Order Id</th>
                                                    <td>{item.id}</td>
                                                </tr>
                                                <tr>
                                                    <th>Order Status</th>
                                                    <td>{item.orderStatus}</td>
                                                </tr>
                                                <tr>
                                                    <th>Payment Mode</th>
                                                    <td>{item.paymentMode}</td>
                                                </tr>
                                                <tr>
                                                    <th>Payment Status</th>
                                                    <td>{item.paymentStatus}</td>
                                                </tr>
                                                <tr>
                                                    <th>Subtotal</th>
                                                    <td>&#8377;{item.subtotal}</td>
                                                </tr>
                                                <tr>
                                                    <th>Shipping</th>
                                                    <td>&#8377;{item.shipping}</td>
                                                </tr>
                                                <tr>
                                                    <th>Total</th>
                                                    <td>&#8377;{item.total}</td>
                                                </tr>
                                                <tr>
                                                    <th>Date</th>
                                                    <td>{new Date(item.date).toLocaleString()}</td>
                                                </tr>
                                            </tbody>
                                        </table>
                                        </div>
                                        </div>
                                        <div className="col-md-8">
                                            <OrderProducts  data={item.products}/>
                                        </div>

                </div>           

                })
           :
           <div className="text-center card p-5">
            <h3>No Found</h3>
           <Link to="/shop" className='btn btn-primary w-25 m-auto'>Shop Now</Link>
           </div>
           
        
        }

         
    </>
  )
}
