
import {React ,useEffect,useState} from 'react'
import { useSelector } from 'react-redux'

import HeroSection from '../Components/HeroSection'
import Testimonials from '../Components/Testimonials'
import Product  from '../Components/Product'

import { getMaincategory } from '../Redux/Actionscreators/MaincategoryActionCreators'
import { getSubcategory } from '../Redux/Actionscreators/SubcategoryActionCreators'

import { getBrand } from '../Redux/Actionscreators/BrandActionCreators'
import { getProduct } from '../Redux/Actionscreators/ProductActionCreators'
import { useDispatch } from 'react-redux'
import { Link, useLocation } from 'react-router-dom'

export default function ShopPage() {
  {/* we have created shop page where we call all the data here  */}
  let [products, setProducts] = useState([])
  let [maincategory, setMaincategory] = useState([])
  let [subcategory, setSubcategory] = useState([])
  let [brand, setBrand] = useState([])
  {/* we have declared these values fore.g {http://localhost:3004/shop?mc=Male} now we HAve to fetch data here after that we will create a function set filter */}
  let [mc, setMc] = useState() //why we store them because we have to click on to links
  let [sc, setSc] = useState()
  let [br, setBr] = useState()

  let [flag, setFlag] = useState(true) //for state change in apply sort filterS
  const [search, setSearch] = useState("")
  const [min, setMin] = useState(0)// these two are for if we have to find product 100 to 1000 price 0 and 1000 is for price range 
  const [max, setMax] = useState(1000)

  let   searchQuery   =useLocation().search // we are using this hook for query selector
  let dispatch = useDispatch()
  let ProductStateData = useSelector(state => state.ProductStateData)
  let MaincategoryStateData = useSelector(state => state.MaincategoryStateData)
  let SubcategoryStateData = useSelector(state => state.SubcategoryStateData)
  let BrandStateData = useSelector(state => state.BrandStateData)


  function applySortFilter(option) {
    if (option === "1")
      setProducts(products.sort((x, y) => y._id.localeCompare(x._id))) // y.id= decendig order locale compare is used to compare string // for latest
  else if (option === "2")
      setProducts(products.sort((x, y) => y.finalPrice - x.finalPrice)) // accending order
  else
      setProducts(products.sort((x, y) => x.finalPrice - y.finalPrice))//decending order

      setFlag(!flag)
  }
  
  // we have to pass mc sc and br as a parameters and we will receive data in it we will  we will fetch mc sc and br from let query = new URLSearchParams(searchQuery)
  function setFilter(mc,sc,br){
    setMc(mc)
    setSc(sc)
    setBr(br) // we will update all categories here 
    applyFilter(mc,sc,br)// we will all apply filter here and go to function where we filter it
    setSearch("")
  }
  // we create  this function it will receive data we will filter all categories here
  function applyFilter(mc,sc,br){
    let data = []
        if (mc === "All" && sc === "All" && br === "All")
            data = ProductStateData.filter((x) => x.active)
        else if (mc !== "All" && sc === "All" && br === "All")
          data = ProductStateData.filter((x) => x.active && x.maincategory.name === mc) // puting all catagores acording their title single filter {singlecondtion one true}
        else if (mc === "All" && sc !== "All" && br === "All")
          data = ProductStateData.filter((x) => x.active && x.subcategory.name === sc)
        else if (mc === "All" && sc === "All" && br !== "All")
          data = ProductStateData.filter((x) => x.active && x.brand.name === br)
        else if (mc !== "All" && sc !== "All" && br === "All")
          data = ProductStateData.filter((x) => x.active && x.maincategory.name === mc && x.subcategory.name === sc)// if combine filter if we need male jeans{double condition 2 true}
        else if (mc !== "All" && sc === "All" && br !== "All")
          data = ProductStateData.filter((x) => x.active && x.maincategory.name === mc && x.brand.name === br)
        else if (mc === "All" && sc !== "All" && br !== "All")
          data = ProductStateData.filter((x) => x.active && x.brand.name === br && x.subcategory.name === sc)
        else
        data = ProductStateData.filter((x) => x.active && x.maincategory.name === mc && x.brand.name === br && x.subcategory.name === sc)//{thiple conditon true}
        setProducts(data)
        if (min === -1 && max === -1)
          setProducts(data)
      else
          setProducts(data.filter((x) => x.finalPrice >= min && x.finalPrice <= max))
  }

  function postSearch(e){
       e.preventDefault()
       setProducts(ProductStateData.filter((x) => x.name?.toLocaleLowerCase().includes(search) || x.maincategory?.name?.toLocaleLowerCase() === search || x.subcategory?.name?.toLocaleLowerCase() === search || x.brand?.name?.toLocaleLowerCase() === search || x.color?.toLocaleLowerCase() === search || x.description?.toLocaleLowerCase().includes(search)))
      
  }
  function postPriceFilter(e) {
    e.preventDefault()
    if (search === "")
        applyFilter(mc, sc, br, min, max)
    else
        postSearchPrice(min, max)
}
function postSearchPrice(min, max) {
  let data = ProductStateData.filter((x) => x.name?.toLocaleLowerCase().includes(search) || x.maincategory?.toLocaleLowerCase() === search || x.subcategory?.toLocaleLowerCase() === search || x.brand?.toLocaleLowerCase() === search || x.color?.toLocaleLowerCase() === search || x.description?.toLocaleLowerCase().includes(search))
  setProducts(data.filter((x) => x.finalPrice >= min && x.finalPrice <= max))
}
  

 useEffect(() => {
        (() => {
            let query = new URLSearchParams(searchQuery) // this line is for  to get search query params
            setFilter(query.get("mc") ?? "All", query.get("sc") ?? "All", query.get("br") ?? "All") //  we have used optional chaining operator here we are fetching data from query parameters=v-21
        })()
    }, [ProductStateData.length, searchQuery])

    useEffect(() => {
        (() => {
            dispatch(getProduct())
        })()
    }, [ProductStateData.length])

  useEffect(() => {
   
      dispatch(getMaincategory())
      if (MaincategoryStateData.length) {
       setMaincategory(MaincategoryStateData.filter((x)=>x.active)) 
       }
   
  }, [MaincategoryStateData.length])
  useEffect(() => { 
    
      dispatch(getSubcategory())
      if (SubcategoryStateData.length) {
        setSubcategory(SubcategoryStateData.filter((x)=>x.active)) 
       }
    
  }, [SubcategoryStateData.length])
  useEffect(() => {
   
      dispatch(getBrand())
      if (BrandStateData.length) {
        setBrand(BrandStateData.filter((x)=>x.active)) 
       }
   
  }, [BrandStateData.length])
  return (
    <>
    <HeroSection title="Shop"/>
  
               <div className="container-fluid my-3">
                              <div className="row">
                                  <div className="col-md-2">
                                      <div className="list-group mb-3">
                                          <Link to={`/shop?mc=All&sc=${sc}&br=${br}`} className="list-group-item list-group-item-action active" aria-current="true">Maincategory</Link>
                                          <Link to={`/shop?mc=All&sc=${sc}&br=${br}`} className="list-group-item list-group-item-action">All</Link>
                                          {
                                              maincategory?.map((item) => {
                                                  return <Link key={item._id} to={`/shop?mc=${item.name}&sc=${sc}&br=${br}`} className="list-group-item list-group-item-action">{item.name}</Link>
                                              })
                                          }
                                      </div>
              
                                      <div className="list-group mb-3">
                                          <Link to={`/shop?mc=${mc}&sc=All&br=${br}`} className="list-group-item list-group-item-action active" aria-current="true">Subcategory</Link>
                                          <Link to={`/shop?mc=${mc}&sc=All&br=${br}`} className="list-group-item list-group-item-action">All</Link>
                                          {
                                              subcategory?.map((item) => {
                                                  return <Link key={item._id} to={`/shop?mc=${mc}&sc=${item.name}&br=${br}`} className="list-group-item list-group-item-action">{item.name}</Link>
                                              })
                                          }
                                      </div>
              
                                      <div className="list-group mb-3">
                                          <Link to={`/shop?mc=${mc}&sc=${sc}&br=All`} className="list-group-item list-group-item-action active" aria-current="true">Brand</Link>
                                          <Link to={`/shop?mc=${mc}&sc=${sc}&br=All`} className="list-group-item list-group-item-action">All</Link>
                                          {
                                              brand?.map((item) => {
                                                  return <Link key={item._id} to={`/shop?mc=${mc}&sc=${sc}&br=${item.name}`} className="list-group-item list-group-item-action">{item.name}</Link>
                                              })
                                          }
                                      </div>
                                      <div className="mb-3">
                                          <h5 className='bg-primary text-center p-2 text-light'>Price Range</h5>
                                          <form onSubmit={postPriceFilter}>
                                              <div className="row">
                                                  <div className="col-md-6 mb-3">
                                                      <label>Min Amount</label>
                                                      <input type="number" name="min" onChange={(e) => setMin(e.target.value)} className='form-control border-3 border-primary' value={min} placeholder='Min Amount' />
                                                  </div>
                                                  <div className="col-md-6 mb-3">
                                                      <label>Max Amount</label>
                                                      <input type="number" name="max" onChange={(e) => setMax(e.target.value)} className='form-control border-3 border-primary' value={max} placeholder='Max Amount' />
                                                  </div>
                                              </div>
                                              <div className="mb-3">
                                                  <button type="submit" className='btn btn-primary w-100'>Apply Filter</button>
                                              </div>
                                          </form>
                                      </div>
                                  </div>
                                  <div className="col-md-10">
                                      <div className="row">
                                          <div className="col-md-9">
                                              <form onSubmit={postSearch}>
                                                  <div className="btn-group w-100">
                                                      <input type="search" name="search" value={search} onChange={(e) => setSearch(e.target.value.toLocaleLowerCase())} placeholder='Search Products by Name, Maincategory, Subcategory, Brand, Color, Description...' className='form-control border-3 border-primary' />
                                                      <button type="submit" className='btn btn-primary'><i className='fa fa-search'></i></button>
                                                  </div>
                                              </form>
                                          </div>
                                          <div className="col-md-3">
                                              <select name="sort" onChange={(e) => applySortFilter(e.target.value)} className='form-select border-3 border-primary'>
                                                  <option value="1">Latest</option>
                                                  <option value="2">Price : High to Low</option>
                                                  <option value="3">Price : Low to High</option>
                                              </select>
                                          </div>
                                      </div>
                                      <Product data={products} title="Shop" />
                                  </div>
                              </div>
                          </div>
              <Testimonials />
    </>
  )
}
