import React from 'react'
import { BrowserRouter,Routes,Route } from 'react-router-dom'
import Navbar from '../Components/Navbar'
import Footer from '../Components/Footer'

import Facts from '../Components/Facts'

import Home from './Home'
import AboutUsPages from './AboutUsPages'
import ContactUsPage from './ContactUsPage'
import FeaturesPage from './FeaturesPage'
import ErrorPage from './ErrorPage'

import TestimonialsPage from './TestimonialsPage'
import ShopPage from './ShopPage'
import SingleProductPage from './SingleProductPage'
import SignupPage from './SignupPage'
import Login from './Login'
import ForgetPasswordPage1 from './ForgetPasswordPage1'
import ForgetPasswordPage2 from './ForgetPasswordPage2'
import ForgetPasswordPage3 from './ForgetPasswordPage3'
import Payment from './Payment'
import CheckoutPage from './CheckoutPage'



import ProfilePage from './ProfilePage'
import Cartpage from './Cartpage'
import UpdateProfile from './UpdateProfile'


 
import AdminHome from '../Admin/Home/AdminHome'


import AdminMaincategory from '../Admin/Maincategory/AdminMaincategory'
import AdminCreateMaincategory from '../Admin/Maincategory/AdminCreateMaincategory'
import AdminUpdateMaincategory from '../Admin/Maincategory/AdminUpdateMaincategory'
    
import AdminSubcategory from '../Admin/Subcategory/AdminSubcategory'
import AdminCreateSubcategory from '../Admin/Subcategory/AdminCreateSubcategory'
import AdminUpdateSubcategory from '../Admin/Subcategory/AdminUpdateSubcategory'

    
import AdminBrand from '../Admin/Brand/AdminBrand'
import AdminCreateBrand from '../Admin/Brand/AdminCreateBrand'
import AdminUpdateBrand from '../Admin/Brand/AdminUpdateBrand'

  
import AdminProduct from '../Admin/Product/AdminProduct'
import AdminCreateProduct from '../Admin/Product/AdminCreateProduct'
import AdminUpdateProduct from '../Admin/Product/AdminUpdateProduct'
 
import AdminTestimonials from '../Admin/Testimonials/AdminTestimonials'
import AdminCreateTestimonials from '../Admin/Testimonials/AdminCreateTestimonials '
import AdminUpdateTestimonials from '../Admin/Testimonials/AdminUpdateTestimonials'
import Confirmation from './Confirmation'
import AdminNewsletter from '../Admin/Newsletter/AdminNewsletter'

import AdminUser from '../Admin/User/AdminUser'
import AdminCreateUser from '../Admin/User/AdminCreateUser'
import AdminUpdateUser from '../Admin/User/AdminUpdateUser'


import AdminContactUs from '../Admin/ContactUs/AdminContactUs'
import AdminContactUsShow from '../Admin/ContactUs/AdminContactUsShow'
import AdminCheckout from '../Admin/Checkout/AdminCheckout'
import AdminCheckoutShow from '../Admin/Checkout/AdminCheckoutShow'














export default function App() {
  return (
    <BrowserRouter>
    <Navbar/>
    
    <Routes>
        <Route path="/" element={<Home/>}/>
             {/* <!-- pages Routs -->*/}
        <Route path="/about" element={<AboutUsPages/>}/>
        <Route path="/contactuspage" element={<ContactUsPage/>}/>
        <Route path="/features" element={<FeaturesPage/>}/>
        <Route path="/errorpage" element={<ErrorPage/>}/>
        <Route path="/testimonials" element={<TestimonialsPage/>}/>
        
        <Route path="/facts"  element={<Facts/>}/>
        <Route  path='/shop' element={<ShopPage/>}/>
        <Route  path="/product/:_id" element={<SingleProductPage/>}/>
        <Route  path="/signup" element={<SignupPage/>}/>
        <Route  path="/login" element={<Login/>}/>
        <Route  path="/forget-password-1" element={<ForgetPasswordPage1/>}/>
         <Route  path="/forget-password-2" element={<ForgetPasswordPage2/>}/>
          <Route  path="/forget-password-3" element={<ForgetPasswordPage3/>}/>
                  <Route  path="/payment/:_id" element={<Payment/>}/>
       

         {/* <!--  Buyer Routs -->*  this conditon is for if the buyer*/}
         {
          localStorage.getItem("login")?
          <>
           <Route  path="/profile" element={<ProfilePage/>}/>
           <Route path='/update-profile' element={<UpdateProfile />}/>
             <Route  path="/cart" element={<Cartpage/>}/>            
           <Route  path="/checkout" element={<CheckoutPage/>}/>
           <Route  path="/confirmation" element={<Confirmation/>}/>
          </>:null
         }

           
         {/* <!--  AdminHome Routs --> these lines for the role is admin*/}
           {
             localStorage.getItem("login") && (localStorage.getItem("role") === "Admin" || localStorage.getItem("role") === "SuperAdmin") ?
        <>
         <Route path="/admin" element={<AdminHome/>}/>
          

          {/* <!--  Admin Maincategory -->*/}
        <Route path="/admin/maincategory" element={<AdminMaincategory/>}/>
        <Route path="/admin/maincategory/create" element={<AdminCreateMaincategory/>}/>
        <Route path="/admin/maincategory/update/:_id" element={ <AdminUpdateMaincategory/>}></Route>

          {/* <!--  AdminSubcategory Routs -->*/}
        <Route path="/admin/subcategory" element={<AdminSubcategory/>}/>
        <Route path="/admin/subcategory/create" element={<AdminCreateSubcategory/>}/>
        <Route path="/admin/subcategory/update/:_id" element={ <AdminUpdateSubcategory/>}></Route>

             {/* <!-- AdminBrand  Routs -->*/}
        <Route path="/admin/brand" element={<AdminBrand/>}/>
        <Route path="/admin/brand/create" element={<AdminCreateBrand/>}/>
        <Route path="/admin/brand/update/:_id" element={ <AdminUpdateBrand/>}></Route>
         
               {/* <!-- AdminProduct  Routs -->*/}
        <Route path="/admin/product" element={<AdminProduct/>}/>
        <Route path="/admin/product/create" element={<AdminCreateProduct/>}/>
        <Route path="/admin/product/update/:_id" element={ <AdminUpdateProduct/>}></Route>
              
              {/* <!-- AdminTestimonials  Routs -->*/}

        <Route path="/admin/testimonials" element={<AdminTestimonials/>}/>
        <Route path="/admin/testimonials/create" element={<AdminCreateTestimonials/>}/>
        <Route path="/admin/testimonials/update/:_id" element={ <AdminUpdateTestimonials/>}></Route>

        <Route path="/admin/newsletter" element={ <AdminNewsletter/>}></Route>

         {/* <!-- Admin  Routs -->*/}
        <Route path="/admin/user" element={ <AdminUser/>}></Route>
      {
                                localStorage.getItem("role") === "SuperAdmin" ?
                                    <>
                                        <Route path='/admin/user/create' element={<AdminCreateUser/>} />
                                        <Route path='/admin/user/update/:_id' element={<AdminUpdateUser/>} />
                                    </> : null
                            }

        <Route path='/admin/contactus' element={<AdminContactUs />} />
        <Route path='/admin/contactus/show/:_id' element={<AdminContactUsShow/>} />
        <Route path='/admin/checkout' element={<AdminCheckout />} />
        <Route path='/admin/checkout/show/:_id' element={< AdminCheckoutShow/>} />

        </> : null
        }


        <Route path='/*' element={<ErrorPage />} /> 
    </Routes>
    
    <Footer/>
    </BrowserRouter>
  )
}




